.main {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title {
    font-size: 1.5em;
    margin-top: 1em;
}

.image {
    height: 33%;
    width: 50%;
    object-fit: contain;
    background-color: gray;

    box-shadow: var(--shadow);
}